body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Roboto",Arial,"Helvetica Neue",Helvetica,sans-serif;
  color: #303030;
}

.light {
  background: #303030;
  min-height: 100%;
}

.dark {
  background: #202020;
  min-height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root {
  min-height: 100%;
  height: 100%;
}

.App {
  max-width: 1346px;
  width: 100%;

  min-height: 100%;
  margin: 0 auto;
}

.header {
 background: none;
 padding: 1em;
 display: flex;
 flex-direction: row;
 align-items: flex-end;
 justify-content: space-between;
 -webkit-user-select: none;
    -moz-user-select: none;
     -ms-user-select: none;
         user-select: none
}

.theme-button {
  font-size: 2em;
  cursor: pointer
}

.logo {
  color: #f5f5f5;
  font-size: 4em;
}

.logo:before {
  content: "\1F428"
}

.main {
  height: 100%;
  display: flex;
  padding: 20px 0 0;
  color: #303030;
  background: #f5f5f5;
}

aside {
  width: 180px;
  padding: 0 1em;
  font-size: 1em;
  font-weight: 500;
}

.headline {
  font-size: 2em;
  font-weight: 700;
}

.line {
  width: 100%;
  margin-bottom: 20px;
  margin-top: 20px;
  border-bottom: 1px solid #dadada;
}

article {
  width: 100%;
  padding: 0 30px 30px 20px;
  border-left: 1px solid #dadada;
  border-left-width: 1px;
  border-left-style: solid;
  border-left-color: rgb(218, 218, 218);
}

.tag {
  padding: 5px 10px;
  border-radius: 5px;
}

.tag:hover {
  color: #e34449;
  cursor: pointer;
  background: #eaeaea
}

.story-item {
  vertical-align: center;
  margin-bottom: 1.5em;
}

.si-heading-container {
  display: flex;
  /* mobiilis flex direction muuta */
  align-items: center
}

.story-item-heading {
  font-size: 1.5em;
  font-weight: 700;
  display: inline
}
.story-item-heading:hover {
  color: #e34449;
  cursor: pointer
}

.story-item-tags {
  display: inline-block;
  margin-left: 0.25em;
  padding-top: 5px
}

.story-item-tag {
  display: inline-block;
  padding: 3px 7px;
  margin-right: 0.4em;
  margin-bottom: 5px;
  border-radius: 5px;
  background: #eaeaea;
  font-size: 1em;
  font-weight: 500;
  cursor: pointer;
  box-shadow: 1px 1px 1px 0px #0000004d;
}
.story-item-tag:hover{
  background: #e34449;
  color: white;
}

.story-item-summary {
  font-style: italic;
  font-size: 1.1em;
  font-weight: 500;
  color: #a0a0a0;
  margin-bottom: 0.4em;
}

.story-item-description{
  font-size: 1.1em;
}
.read-more{
  display: inline;
  text-decoration: none;
  font-weight: 900;
  color: #303030;
  cursor: pointer
}

.read-more:hover{
  color: #e34449
}

.story-item-bottom {
  display: flex;
  color: #a0a0a0;
  margin: 0.6em auto;
  font-size: 0.9em;
  font-weight: 600
}

.story-item-bottom > * {
  margin-right: 0.5em
}

.story-text {
  white-space: pre-wrap;
  font-size: 1.1em;
}

.back-button {
  cursor: pointer
}

.back-button:hover{
  color: #e34449
}

.Pagination {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: flex;
  flex-flow: row wrap;
  justify-content: center
}

.pagination-button {
  text-align: center;
  box-shadow: 1px 1px 1px 0px #0000004d;
  color: #303030;
  background: #eaeaea;
  min-width: 1em;
  border-radius: 5px;
  font-weight: 600;
  font-size: 1.1em;
  padding: 0.5em 1em;
  margin: 0.5em;
  cursor: pointer;
}
.pagination-button:hover{
  background: #dadada
}

.pagination-button.current {
  background: #303030;
  color: #dadada
}

.pagination-button.next {
  font-weight: 600;
  background: #e34449;
  color: #efefef
}

.pagination-button.next:hover{
  background: #c72d32
}
.pagination-button.back {
  font-weight: 600;
  background: #e34449;
  color: #efefef
}

.pagination-button.back:hover{
  background: #c72d32
}

.header-items-container {
  display: flex;
  flex-direction: column-reverse;
  align-items: flex-end;
}

.input {
  outline: none;
  padding: 0.3em 0.5em;
  border: 1px solid #ccc;
  border-radius: 5px;
  background: #fff;
  color: #303030;
  font-size: 1.3em;
  /* temp margin */
  margin-top: 1em; 
}

.input::-webkit-input-placeholder { 
  color: #dadada;
}

.input::-ms-input-placeholder { 
  color: #dadada;
}

.input::placeholder { 
  color: #dadada;
}

.footer {
  color: #303030;
  padding: 1em;
  border-top: 1px solid #dadada;
  background: whitesmoke;
  font-size: 1.1em;
}
